'use client';

import React, { useState, useEffect } from 'react';
import {
    DegreeDomain,
    Grade,
    Level
} from '@/lib/grading';
import { Button } from '@/components/ui/button';
import { LayoutGrid, Calculator as CalcIcon, GraduationCap, ChevronRight } from 'lucide-react';
import { Toaster } from 'sonner';
import CurrentCGPADashboard from './CurrentCGPADashboard';
import PredictiveCGPADashboard from './PredictiveCGPADashboard';

type UserGrades = Record<DegreeDomain, Record<string, Grade>>;
type ManuelOverrides = Record<DegreeDomain, Record<string, Grade>>;

export default function Calculator() {
    const [view, setView] = useState<'current' | 'predictive'>('current');
    const [domain, setDomain] = useState<DegreeDomain>('Data Science');
    const [level, setLevel] = useState<Level>('Foundation');
    const [userGrades, setUserGrades] = useState<UserGrades>({
        'Data Science': {},
        'Electronic Systems': {}
    });
    const [overrides, setOverrides] = useState<ManuelOverrides>({
        'Data Science': {},
        'Electronic Systems': {}
    });
    const [isInitialized, setIsInitialized] = useState(false);

    // Load from localStorage on mount
    useEffect(() => {
        const saved = localStorage.getItem('iitm-cgpa-v4');
        if (saved) {
            try {
                const parsed = JSON.parse(saved);
                if (parsed.userGrades) setUserGrades(parsed.userGrades);
                if (parsed.overrides) setOverrides(parsed.overrides);
                if (parsed.domain) setDomain(parsed.domain);
                if (parsed.level) setLevel(parsed.level);
            } catch {
                console.error('Failed to load storage');
            }
        }
        setIsInitialized(true);
    }, []);

    // Save to localStorage
    useEffect(() => {
        if (isInitialized) {
            localStorage.setItem('iitm-cgpa-v4', JSON.stringify({ userGrades, overrides, domain, level }));
        }
    }, [userGrades, overrides, domain, level, isInitialized]);

    const updateGrade = (courseName: string, grade: Grade) => {
        setUserGrades(prev => ({
            ...prev,
            [domain]: { ...prev[domain], [courseName]: grade }
        }));
    };

    const updateOverride = (courseName: string, grade: Grade) => {
        setOverrides(prev => ({
            ...prev,
            [domain]: { ...prev[domain], [courseName]: grade }
        }));
    };

    const clearOverrides = () => {
        setOverrides(prev => ({
            ...prev,
            [domain]: {}
        }));
    };

    if (!isInitialized) return null;

    return (
        <div className="min-h-screen bg-background text-foreground font-sans selection:bg-emerald-100 selection:text-emerald-900">
            <Toaster position="top-center" richColors />

            <header className="fixed top-0 left-0 right-0 z-50 transition-all duration-300">
                <div className="backdrop-blur-xl bg-white/70 border-b border-slate-100 px-6 py-4 max-w-7xl mx-auto flex items-center justify-between shadow-sm">
                    <div className="flex items-center gap-3">
                        <div className="w-10 h-10 bg-emerald-600 rounded-2xl flex items-center justify-center shadow-lg shadow-emerald-200">
                            <GraduationCap className="text-white w-6 h-6" />
                        </div>
                        <div>
                            <h2 className="text-lg font-black text-slate-900 tracking-tight leading-none">Academia Planner</h2>
                            <p className="text-[10px] font-bold text-slate-400 uppercase tracking-widest mt-1">IIT Madras BS Official</p>
                        </div>
                    </div>

                    <nav className="flex items-center gap-2 p-1.5 bg-slate-100 rounded-2xl">
                        <Button
                            variant={view === 'current' ? 'default' : 'ghost'}
                            size="sm"
                            onClick={() => setView('current')}
                            className={`rounded-xl px-4 font-black text-xs transition-all ${view === 'current' ? 'bg-white text-emerald-600 shadow-sm border border-slate-100 hover:bg-white' : 'text-slate-500'}`}
                        >
                            <LayoutGrid className="w-3.5 h-3.5 mr-2" /> Current Status
                        </Button>
                        <Button
                            variant={view === 'predictive' ? 'default' : 'ghost'}
                            size="sm"
                            onClick={() => setView('predictive')}
                            className={`rounded-xl px-4 font-black text-xs transition-all ${view === 'predictive' ? 'bg-white text-emerald-600 shadow-sm border border-slate-100 hover:bg-white' : 'text-slate-500'}`}
                        >
                            <CalcIcon className="w-3.5 h-3.5 mr-2" /> Predictive Model
                        </Button>
                    </nav>
                </div>
            </header>

            <main className="pt-28 pb-20 px-4 md:px-8 max-w-6xl mx-auto space-y-12">
                {view === 'current' ? (
                    <div className="space-y-10">
                        <div className="max-w-xl">
                            <h1 className="text-4xl font-extrabold text-slate-900 tracking-tight mb-3 italic">Calculated Precision.</h1>
                            <p className="text-slate-500 font-medium leading-relaxed">
                                Enter your official course grades to track your current CGPA. This ensures perfect adherence
                                to the IITM BS Degree grading policy.
                            </p>
                            <div className="mt-8">
                                <Button
                                    onClick={() => setView('predictive')}
                                    className="rounded-2xl bg-slate-900 hover:bg-slate-800 text-white px-8 py-6 h-auto font-black text-sm group"
                                >
                                    Predict Next Term CGPA <ChevronRight className="ml-2 w-4 h-4 group-hover:translate-x-1 transition-transform" />
                                </Button>
                            </div>
                        </div>

                        <CurrentCGPADashboard
                            domain={domain}
                            level={level}
                            userGrades={userGrades}
                            updateGrade={updateGrade}
                            setDomain={setDomain}
                            setLevel={setLevel}
                        />
                    </div>
                ) : (
                    <div className="space-y-10">
                        <div className="max-w-xl">
                            <h1 className="text-4xl font-extrabold text-slate-900 tracking-tight mb-3 italic">Predictive Intelligence.</h1>
                            <p className="text-slate-500 font-medium leading-relaxed">
                                Leverage our locally-hosted ML model to estimate future outcomes. Adjust performance sliders
                                and simulate &quot;what-if&quot; scenarios for upcoming subjects.
                            </p>
                        </div>

                        <PredictiveCGPADashboard
                            domain={domain}
                            userGrades={userGrades}
                            overrides={overrides}
                            updateOverride={updateOverride}
                            clearOverrides={clearOverrides}
                        />
                    </div>
                )}
            </main>

            {/* Footer */}
            <footer className="border-t border-slate-100 py-10 px-8 text-center text-slate-400">
                <p className="text-[10px] font-black uppercase tracking-[0.3em] mb-4">100% Client-Side Computation  Privacy Focused</p>
                <p className="text-xs font-medium">IIT Madras BS Program  Academic Planning System &copy; 2026</p>
            </footer>
        </div>
    );
}
