'use client';

import React, { useMemo } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectGroup, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { CheckCircle2, Circle, TrendingUp } from 'lucide-react';
import {
    Course,
    DegreeDomain,
    Grade,
    Level,
    GRADE_POINTS,
    calculateCGPA,
    CURRICULUM
} from '@/lib/grading';
import { motion, AnimatePresence } from 'framer-motion';

interface Props {
    domain: DegreeDomain;
    level: Level;
    userGrades: Record<DegreeDomain, Record<string, Grade>>;
    updateGrade: (courseName: string, grade: Grade) => void;
    setDomain: (domain: DegreeDomain) => void;
    setLevel: (level: Level) => void;
}

export default function CurrentCGPADashboard({
    domain,
    level,
    userGrades,
    updateGrade,
    setDomain,
    setLevel
}: Props) {
    const currentViewCourses = useMemo(() => {
        const officialList = CURRICULUM[domain][level];

        return officialList.map(c => ({
            id: `${domain}-${level}-${c.name}`,
            name: c.name,
            credits: c.credits,
            level: level,
            domain: domain,
            cluster: c.cluster,
            grade: userGrades[domain][c.name] ?? null
        }));
    }, [domain, level, userGrades]);

    const stats = useMemo(() => {
        const completed: Course[] = [];
        (['Foundation', 'Diploma', 'BSc Degree', 'BS Degree'] as Level[]).forEach(l => {
            CURRICULUM[domain][l].forEach(c => {
                const g = userGrades[domain][c.name];
                if (g) {
                    completed.push({
                        id: `c-${c.name}`,
                        name: c.name,
                        credits: c.credits,
                        level: l,
                        domain: domain,
                        cluster: c.cluster,
                        grade: g
                    });
                }
            });
        });
        return calculateCGPA(completed);
    }, [domain, userGrades]);

    return (
        <div className="space-y-6 animate-in fade-in duration-500">
            {/* Header / Stats Summary */}
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                <Card className="md:col-span-2 border-none bg-emerald-600 text-white rounded-[2rem] shadow-xl overflow-hidden relative">
                    <div className="absolute top-0 right-0 w-32 h-32 bg-white/10 rounded-full blur-3xl -mr-16 -mt-16"></div>
                    <CardHeader className="pb-2">
                        <CardTitle className="text-xs font-black uppercase tracking-[0.2em] opacity-80">Current Academic Standing</CardTitle>
                    </CardHeader>
                    <CardContent>
                        <div className="flex items-baseline gap-4">
                            <span className="text-6xl font-black tracking-tighter">{stats.cgpa}</span>
                            <div className="space-y-1">
                                <p className="text-sm font-bold opacity-80">Current CGPA</p>
                                <p className="text-[10px] font-medium opacity-60 uppercase tracking-widest">{stats.courseCount} Courses Completed</p>
                            </div>
                        </div>
                    </CardContent>
                </Card>

                <div className="space-y-4">
                    <div className="bg-white p-4 rounded-3xl border border-slate-100 shadow-sm space-y-2">
                        <Label className="text-[10px] font-black text-slate-400 uppercase tracking-widest px-1">Program</Label>
                        <Select value={domain} onValueChange={(v: DegreeDomain) => setDomain(v)}>
                            <SelectTrigger className="h-10 rounded-xl border-slate-100 bg-slate-50/50 font-bold text-xs">
                                <SelectValue />
                            </SelectTrigger>
                            <SelectContent className="rounded-xl">
                                <SelectItem value="Data Science">Data Science</SelectItem>
                                <SelectItem value="Electronic Systems">Electronic Systems</SelectItem>
                            </SelectContent>
                        </Select>
                    </div>
                    <div className="bg-white p-4 rounded-3xl border border-slate-100 shadow-sm space-y-2">
                        <Label className="text-[10px] font-black text-slate-400 uppercase tracking-widest px-1">Curriculum Level</Label>
                        <Select value={level} onValueChange={(v: Level) => setLevel(v)}>
                            <SelectTrigger className="h-10 rounded-xl border-slate-100 bg-slate-50/50 font-bold text-xs">
                                <SelectValue />
                            </SelectTrigger>
                            <SelectContent className="rounded-xl">
                                <SelectItem value="Foundation">Foundation</SelectItem>
                                <SelectItem value="Diploma">Diploma</SelectItem>
                                <SelectItem value="BSc Degree">BSc Degree</SelectItem>
                                <SelectItem value="BS Degree">BS Degree</SelectItem>
                            </SelectContent>
                        </Select>
                    </div>
                </div>
            </div>

            {/* Course Table */}
            <Card className="rounded-[2.5rem] border-none shadow-2xl shadow-slate-200/50 overflow-hidden bg-white">
                <CardHeader className="p-8 pb-4">
                    <div className="flex items-center gap-3">
                        <div className="p-2 bg-slate-100 rounded-xl">
                            <TrendingUp className="w-5 h-5 text-emerald-600" />
                        </div>
                        <div>
                            <CardTitle className="text-xl font-black text-slate-900">{level} Progress</CardTitle>
                            <p className="text-xs font-medium text-slate-500 uppercase tracking-wider">Log your official grades below</p>
                        </div>
                    </div>
                </CardHeader>
                <CardContent className="p-0">
                    <div className="overflow-x-auto">
                        <Table>
                            <TableHeader className="bg-slate-50/50">
                                <TableRow className="border-slate-50 hover:bg-transparent">
                                    <TableHead className="w-[400px] pl-8 text-[10px] font-black text-slate-400 uppercase tracking-[0.2em]">Subject Name</TableHead>
                                    <TableHead className="text-center text-[10px] font-black text-slate-400 uppercase tracking-[0.2em]">Credits</TableHead>
                                    <TableHead className="text-right pr-8 text-[10px] font-black text-slate-400 uppercase tracking-[0.2em]">Grade Obtained</TableHead>
                                </TableRow>
                            </TableHeader>
                            <TableBody>
                                <AnimatePresence mode="popLayout">
                                    {currentViewCourses.map((course) => (
                                        <motion.tr
                                            key={course.id}
                                            initial={{ opacity: 0, y: 10 }}
                                            animate={{ opacity: 1, y: 0 }}
                                            className="group border-slate-50 hover:bg-slate-50/40 transition-all"
                                        >
                                            <TableCell className="pl-8 py-5">
                                                <div className="flex items-center gap-3">
                                                    {course.grade ? (
                                                        <CheckCircle2 className="w-4 h-4 text-emerald-500" />
                                                    ) : (
                                                        <Circle className="w-4 h-4 text-slate-200" />
                                                    )}
                                                    <div className="space-y-0.5">
                                                        <span className={`font-black text-sm tracking-tight ${course.grade ? 'text-slate-900' : 'text-slate-400'}`}>
                                                            {course.name}
                                                        </span>
                                                        <p className="text-[10px] font-bold text-slate-400 uppercase">{course.cluster}</p>
                                                    </div>
                                                </div>
                                            </TableCell>
                                            <TableCell className="text-center">
                                                <span className="text-xs font-black text-slate-500">{course.credits}</span>
                                            </TableCell>
                                            <TableCell className="text-right pr-8">
                                                <Select
                                                    value={course.grade ?? "not_taken"}
                                                    onValueChange={(v) => updateGrade(course.name, v === "not_taken" ? null : v as Grade)}
                                                >
                                                    <SelectTrigger className={`w-32 h-9 ml-auto border-none bg-slate-50 rounded-xl focus:ring-0 font-black text-[10px] ${course.grade ? 'bg-emerald-50 text-emerald-700' : 'text-slate-400'}`}>
                                                        <SelectValue placeholder="Not Taken" />
                                                    </SelectTrigger>
                                                    <SelectContent className="rounded-xl border-slate-100 shadow-xl">
                                                        <SelectItem value="not_taken" className="text-slate-400 italic">Not Taken</SelectItem>
                                                        <SelectGroup>
                                                            {['S', 'A', 'B', 'C', 'D', 'E'].map(g => (
                                                                <SelectItem key={g} value={g} className="font-black">
                                                                    Grade {g} ({GRADE_POINTS[g as Exclude<Grade, null>]})
                                                                </SelectItem>
                                                            ))}
                                                        </SelectGroup>
                                                        <SelectGroup>
                                                            {['U', 'I', 'W'].map(g => (
                                                                <SelectItem key={g} value={g} className="text-slate-400 italic">
                                                                    {g === 'U' ? 'U (Fail)' : g === 'I' ? 'I (Incomplete)' : 'W (Withdrawn)'}
                                                                </SelectItem>
                                                            ))}
                                                        </SelectGroup>
                                                    </SelectContent>
                                                </Select>
                                            </TableCell>
                                        </motion.tr>
                                    ))}
                                </AnimatePresence>
                            </TableBody>
                        </Table>
                    </div>
                </CardContent>
            </Card>
        </div>
    );
}
